
.skeleton:empty {
    width: 500px;
    height: 100vh; /* change height to see repeat-y behavior */
    position: relative;
    align-self: center;
    margin: 2vw auto 0 auto;
    background-image:
            radial-gradient( circle 50px at 50px 50px, #171717 99%, transparent 0 ),
            linear-gradient( 90deg, rgba(255, 255, 255, 0), rgba(49, 49, 49, 0.5) 50%, rgba(255, 255, 255, 0) 80% ),
            linear-gradient( #282828 20px, transparent 0 ),
            linear-gradient( #272727 20px, transparent 0 ),
            linear-gradient( #252525 20px, transparent 0 ),
            linear-gradient( #212121 20px, transparent 0 );

    background-repeat: repeat-y;

    background-size:
            100px 200px, /* circle */
            50px 200px, /* highlight */
            150px 200px,
            350px 200px,
            300px 200px,
            250px 200px;

    background-position:
            0 0, /* circle */
            0 0, /* highlight */
            120px 0,
            120px 40px,
            120px 80px,
            120px 120px;

    animation: shine 3s infinite;
}

@keyframes shine {
    to {
        background-position:
                0 0,
                100% 0, /* move highlight to right */
                120px 0,
                120px 40px,
                120px 80px,
                120px 120px;
    }
}

@media (max-width: 500px) {
    .skeleton:empty {
        margin-left: 10px;
        margin-top: 20px;
        overflow: hidden;
        width: 100%;
        background-size: 50% 200px, /* circle */
        20vw 200px, /* highlight */
        30vw 200px,
        62vw 200px,
        50vw 200px,
        40vw 200px;

        background-image:
                radial-gradient( circle 40px at 50px 50px, #171717 99%, transparent 0 ),
                linear-gradient( 90deg, rgba(255, 255, 255, 0), rgba(49, 49, 49, 0.5) 50%, rgba(255, 255, 255, 0) 80% ),
                linear-gradient( #282828 20px, transparent 0 ),
                linear-gradient( #272727 20px, transparent 0 ),
                linear-gradient( #252525 20px, transparent 0 ),
                linear-gradient( #212121 20px, transparent 0 );
    }
}

@media (max-width: 400px) {
    .skeleton:empty {
        background-size: 50% 200px, /* circle */
        20vw 200px, /* highlight */
        20vw 200px,
        50vw 200px,
        40vw 200px,
        30vw 200px;
    }
}
